'use strict';

import GGBView from "./views/GeogebraView.js";

const app_container = document.querySelector(".app-container");
const menu_item = document.querySelector('.sidebar');
const ggb_element = document.querySelector('#ggb-element');
const welcome = document.querySelector('.welcome');


const get_data = function(url, data){
	fetch(url, {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json',
		},
		body: JSON.stringify(data)
	})
	.then(response => response.json)
	.then(data => data);
}

GGBView.renderGGB(ggb_element);



menu_item.addEventListener('click', (e) => {
	// Desplega la vista de árbol
	if(e.target.classList.contains('menu')){
		let unit = e.target.dataset.unit;
		//console.log(document.querySelector(`.sidebar__list--${unit}`).classList)
		document.querySelector(`.sidebar__list--${unit}`).classList.toggle("hidden")
	}
	// Obtiene el objeto seleccionado
	else if(e.target.classList.contains('select')){
		welcome.classList.add('hidden');
		let text = e.target.textContent;
		let id = e.target.parentElement.dataset.id;
		let name = e.target.dataset.name;
		let url = `http://127.0.0.1:8000/ans/unidad${id}/${name}/`;

		if(text != 'Método Gráfico'){
			ggb_element.classList.add('hidden');

		}else{
			// El método gráfico únicamente desplegará el applet de geogebra
			ggb_element.classList.remove('hidden');
		}
	}
});








